body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f9f9f9;
}

.app-container {
  max-width: 900px;
  margin: auto;
  padding: 20px;
}

header {
  text-align: center;
  padding: 20px 0;
}

.subtitle {
  color: #777;
  font-size: 1rem;
}

.reviews-container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  margin-top: 20px;
}

.review-card {
  background-color: #fff;
  border-radius: 8px;
  padding: 16px 20px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.05);
  border-left: 5px solid #4a90e2;
}

.review-card h2 {
  margin-top: 0;
  font-size: 1.25rem;
  color: #333;
}

.review-card ul {
  padding-left: 1rem;
}

.review-card li {
  background: #ffecec;
  padding: 6px 10px;
  margin: 5px 0;
  border-radius: 4px;
  color: #d8000c;
}

.no-issues {
  color: green;
  font-weight: bold;
}
